import * as React from 'react';
import { useLocation } from 'react-router-dom';
import { Wrapper, Logo, MenuLeft, MenuRight, NavLinkList, NavLink } from './styles';
import ThemeWrapper from '../ThemeWrapper';
import { ButtonExternal } from '../Button';

const Header: React.FC = () => {
  let location = useLocation();

  return (
    <ThemeWrapper>
      <Wrapper>
        <nav>
          <NavLinkList>
            <MenuLeft>
              <Logo href="/">L-o-G</Logo>
              <li>
                <NavLink href="/" isActive={location.pathname === '/'}>
                  Home
                </NavLink>
              </li>
              <li>
                <NavLink href="/news" isActive={location.pathname === '/news'}>
                  News List
                </NavLink>
              </li>
            </MenuLeft>
            <MenuRight>
              <li>
                <ButtonExternal href="/login" size='medium' variant='secondary'>
                  {location.pathname === '/profile' ? 'Log Out' : 'Login'}
                </ButtonExternal>
              </li>
            </MenuRight>
          </NavLinkList>
        </nav>
      </Wrapper>
    </ThemeWrapper>
  );
};

export default Header;
